@import "../../globalSASS/global.scss";
.field {
    width: 596px;
    height: 58px;

    font-family: 'Roboto Condensed', sans-serif;
    font-size: 18px;
    padding-left: 30px;

    background-color: #F8F8F8;
    border: 1px solid #B2B2B2;

    transition: 0.5s;

    &:focus {
        background-color: #fff;
    }

    &__choice {
        width: 180px;
        height: 30px;

        font-size: 16px;
        padding-left: 10px;
    }

    &_size {
        &_s {
            width: 168px;
        }
    }

    &_output {
        height: auto;
        font-family: 'Times New Roman', Times, serif;
    }

    
}