@import "../../globalSASS/global.scss";
.literature {
    width: 626px;

    &__title {
        padding: 0;
        margin: 0;
        font-size: 24px;
        font-weight: normal;
        text-transform: uppercase;
    }

    &__choice {
        margin-top: 30px;
        padding-left: 10px;
    }

    &__output {
        margin-top: 50px;
        margin-bottom: 60px;
    }

    &__dash {
        margin-left: 30px;
    }

    &__example{
        border-bottom: 1px dashed #000;
        cursor: pointer;
    }
}