@import "../../globalSASS/global.scss";
.side {
	margin-top: 30px;

	width: 100%;

	&_size_s {
		width: 197px;
    }
    
    &__edition {
        display: flex;
        justify-content: space-between;
        width: 100%;
    }

    &__entry {
        margin-top: 5px;
    }

    &__check {
        margin-top: 5px;

        display: flex;
        align-items: center;

        cursor: pointer;
    }

    &__checkbox {
        margin-right: 5px;
    }

    &__button {
        margin-top: 30px;

        width: 416px;
        height: 60px;

        font-family: 'Roboto Condensed', sans-serif;
        font-size: 18px;
        font-weight: bold;
        color: #FFF;

        background-color: #E9B370;
        border: 1px solid #E9B370;

        cursor: pointer;
        transition: 0.3s;

        &:hover {
            background-color: #FFF;
            color: #E9B370;
        }

        &:active {
            transform: scale(0.98);
            outline: none;
        }
    }
}
